import React, { useEffect, useState } from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import CheckIcon from '@material-ui/icons/Check';

import Avatar from '@material-ui/core/Avatar';
import { Experiences, Experience } from '../../appTypes';

import moment from 'moment/moment';
import 'moment/locale/en-gb';
import { List, ListItem, ListItemText, ListItemIcon } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        },
        buttons2: {
            textTransform: "none"
        }        
    }),
);

const ExperiencePage: React.FC<Experiences> = (props): JSX.Element => {

    const classes = useStyles();

    const [list, setList] = useState<Array<Experience>>([]);

    useEffect(() => {
        setList(props.data);
    }, [props.data]);

    if (list.length > 0) {
        return (
            <React.Fragment>
                <Box mt={2}>
                    <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Experiences</Typography>
                </Box>
                <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                    {list.length > 0 ?
                        list.map((work, index) => (
                            <Box p={1} key={index} flexGrow={1}>
                                <Card>
                                    <CardHeader
                                        avatar={<LazyLoad placeholder={<Loader />}><Avatar
                                            variant="rounded"
                                            className={classes.medium}
                                            alt={work.company}
                                            src={work.logo} /></LazyLoad>}
                                        title={work.company}
                                        subheader={work.position + ' (' + moment(work.startDate).format('MMM YYYY') + ', ' +
                                            moment(work.endDate).format('MMM YYYY') + ')'}
                                        action={work.location} />
                                    <CardContent>
                                        <List dense={true}>
                                            {work.summary.split('\n')
                                                .map((sentence, index) => (
                                                    <ListItem key={index}>
                                                        <ListItemIcon><CheckIcon /></ListItemIcon>
                                                        <ListItemText primary={sentence}></ListItemText>
                                                    </ListItem>
                                                ))}
                                        </List>
                                    </CardContent>
                                    <CardActions>
                                        <Box width="100%" textAlign="right">
                                            <Button
                                                className={classes.buttons}
                                                href={work.website}
                                                startIcon={<OpenInNewIcon />}
                                                target="_blank"
                                                rel="noopener"
                                                size="small"
                                                variant="outlined">Company Website</Button>
                                        </Box>
                                    </CardActions>
                                </Card>
                            </Box>
                        )) : <Loader />
                    }
                </Box>
                <Box mt={2} pb={2}>
                    <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                    <p>I worked for a short period in the companies below, but I contributed to important projects.</p>
                    <List>
                        <ListItem>
                            <ListItemIcon><CheckIcon /></ListItemIcon>
                            <ListItemText primary={<Button endIcon={<OpenInNewIcon />} className={classes.buttons2}
                                    href="http://www.robutel.com/" target="_blank" rel="noopener" color="secondary">Robutel</Button>}
                                secondary={
                                    <React.Fragment>
                                        <Typography component="span" variant="body2" color="textPrimary">
                                            contributed to ROFPP - C++ Programming Framework as a software engineer
                                        </Typography>
                                    </React.Fragment>
                                } />
                        </ListItem>
                        <ListItem>
                            <ListItemIcon><CheckIcon /></ListItemIcon>
                            <ListItemText primary={<Button endIcon={<OpenInNewIcon />} className={classes.buttons2}
                                    href="https://www.epttavm.com" target="_blank" rel="noopener" color="secondary">ePTTAVM</Button>}
                                secondary={
                                    <React.Fragment>
                                        <Typography component="span" variant="body2" color="textPrimary">
                                            contributed to e-commerce application 
                                            <Button endIcon={<OpenInNewIcon />} href="https://www.epttavm.com" className={classes.buttons} target="_blank" rel="noopener" color="secondary">https://www.epttavm.com</Button> 
                                            as a backend developer
                                        </Typography>
                                    </React.Fragment>
                                } />
                        </ListItem>
                    </List>
                    <p>I also worked as a part-time lecturer in some computer education centers in Ankara, Turkey before 2010. 
                        I gave C, Algorithm, PHP, Java, Dreamweaver, Fireworks, HTML, CSS and Advanced Database Programming lessons.</p>
                </Box>
            </React.Fragment>
        );
    }
    else {
        return (
            <Loader />
        );
    }

};

export default ExperiencePage;