import React, { useState, useEffect } from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import DetailsIcon from '@material-ui/icons/Details';
import CheckIcon from '@material-ui/icons/Check';

import Avatar from '@material-ui/core/Avatar';
import { Projects, Project } from '../../appTypes';
import { Chip, ListItemIcon, ListItemText, List, ListItem } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        media: {
            backgroundSize: 'contain',
            height: 0,
            paddingTop: '40%', // 16:9
        },
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        },
    }),
);

const ProjectsPage: React.FC<Projects> = (props): JSX.Element => {

    const classes = useStyles();

    const [list, setList] = useState<Array<Project>>([]);

    useEffect(() => {
        setList(props.data);
    }, [props.data]);

    return (
        <React.Fragment>
            <Box mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Projects</Typography>
            </Box>
            <Box display="flex" flexWrap="wrap" justifyContent="center" p={1} m={1}>
                {list.length > 0 ?
                    list.map((project, index) => (
                        <Box p={1} key={index}>
                            <Card>
                                <CardHeader
                                    avatar={<LazyLoad once placeholder={<Loader />}><Avatar
                                        variant="rounded"
                                        className={classes.medium}
                                        alt={project.displayName}
                                        src={project.logo} /></LazyLoad>}
                                    title={project.displayName}
                                    action={<Chip label={project.languages.join(', ')} size="small" />} />
                                <CardMedia
                                    className={classes.media}
                                    image={project.images[0].resolutions.thumbnail.url}
                                    title={project.displayName} />
                                <CardContent>
                                    <Typography variant="body2" color="textPrimary" component="p">
                                        {project.summary}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Box width="100%" display="flex">
                                        <Box flexGrow={1}>
                                            <Button
                                                className={classes.buttons}
                                                href={'/#/projects/' + (index + 1)}
                                                startIcon={<DetailsIcon />}
                                                size="small"
                                                variant="outlined">Learn More</Button>
                                        </Box>
                                        <Box textAlign="right">
                                            <Button
                                                className={classes.buttons}
                                                href={project.website}
                                                startIcon={<OpenInNewIcon />}
                                                target="_blank"
                                                rel="noopener"
                                                size="small"
                                                variant="outlined">Project Website</Button>
                                        </Box>
                                    </Box>
                                </CardActions>
                            </Card>
                        </Box>
                    )) : <Loader />}
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                <List dense={true}>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="OPL-STR"
                            secondary={<React.Fragment>
                                Summary: Mobile Warehouse Robot.<br />
                                    Description: Project information is confidential.<br />
                                    Role: Team Lead<br />
                                    Notes: Non-confidential information can be provided if requested in person.</React.Fragment>} />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="TKDK Erozyon"
                            secondary={<React.Fragment>
                                Summary: Online application and tracking system to assist farmers who apply for Soil Cover Management and Soil Erosion Control support.<br />
                                    Role: Full Stack Developer<br />
                                    Technologies: ASP.NET MVC 5.<br />
                                    Status: Terminated due to maintenance problems.</React.Fragment>} />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="HÄ±pÄ±r, MobilOyuncu, Samsung Apps"
                            secondary={<React.Fragment>
                                Summary: Java games and multimedia market application for old java based mobile phones.<br />
                                    Role: Full Stack Developer<br />
                                    Technologies: Spring MVC.<br />
                                    Status: Terminated due to maintenance problems.</React.Fragment>} />
                    </ListItem>
                </List>
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Personal</Typography>
                <List dense={true}>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Rover Robot Angela"
                            secondary={<React.Fragment>
                                Summary: Custom Nasa JPL's Open Source Rover Robot.<br />
                                    Description: I used this template for 5 enterprise applications.<br />
                                    Technologies: ROS, Solidworks<br />
                                    Status: Failed due to weak 3d printed chassis.</React.Fragment>} />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Enterprise Web App Template for ASP.NET MVC 5"
                            secondary={<React.Fragment>
                                Summary: Monolitich boilerplate for .net mvc 5 applications.<br />
                                    Description: I used this template for 5 enterprise applications.<br />
                                    Technologies: Code First with Fluent API, i10n, i18n, identity 2, di, theming, multi db<br />
                                    Status: Terminated due to much more modern technologies.</React.Fragment>} />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="R2D2 Web Engine"
                            secondary={<React.Fragment>
                                Summary: PHP based web CMS like Wordpress.<br />
                                    Description: I used this CMS for many small company or personal websites between 2007-2010.<br />
                                    Status: Terminated due to inevitable CMS Wordpress.</React.Fragment>} />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Sikloid"
                            secondary={<React.Fragment>
                                Summary: Graphic program which draws some kinds of cycloids with parametric function entry.<br />
                                    Description: I wrote this application for academic purposes in university in 2004.<br />
                                    Technologies: Both Java and Visual Basic versions<br />
                                    Notes: I had written this application, before I started working as a software engineer.
                                    </React.Fragment>} />
                    </ListItem>
                </List>
            </Box>
        </React.Fragment>
    );
};

export default ProjectsPage;