import React, { useEffect, useState } from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { Box } from '@material-ui/core'
import { Basics } from '../../appTypes';
import Loader from '../../components/Loader/Loader';
import LazyLoad from 'react-lazyload';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        large: {
            width: theme.spacing(20),
            height: theme.spacing(20),
        },
    }),
);


const AboutMePage: React.FC<Basics> = (props): JSX.Element => {

    const classes = useStyles();

    const [list, setList] = useState<Array<string>>([]);

    useEffect(() => {
        if (props.data.summary !== '') {
            setList(props.data.summary.split('\n'));
        }
    }, [props.data]);

    return (
        <Typography component="div">
            <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                <Box p={1}>
                    <LazyLoad placeholder={<Loader />}><Avatar alt={props.data.name} src={props.data.picture} className={classes.large} /></LazyLoad>
                </Box>
                <Box p={1} flexWrap={1}>
                    <Typography variant="subtitle1" color="textPrimary" component="h2">Summary</Typography>
                    {list.length > 0 ?
                        list.map((text, index) => (
                            <Box key={index} m={1}>
                                - {text}
                            </Box>
                        )) :
                        <Loader />
                    }
                    <Box mt={2}>
                        <Typography variant="subtitle1" color="textPrimary" component="h2">Hobbies and likes</Typography>
                        <ul>
                            <li>robotics</li>
                            <li>philosophy</li>
                            <li>psychology</li>
                            <li>poetry</li>
                            <li>fishing</li>
                        </ul>
                        <Typography variant="subtitle1" color="textPrimary" component="h2">Descriptive notes about me</Typography>
                        <ul>
                            <li>Passion for science</li>
                            <li>Self-teaching and love of learning</li>
                            <li>Hidden experience</li>
                            <li>Variety of technologies</li>
                            <li>Formal qualifications</li>
                            <li>Fast adaptation to new technologies</li>
                        </ul>
                    </Box>
                </Box>
            </Box>
        </Typography>
    );
};

export default AboutMePage;