import React, { useState, useEffect } from 'react';

import { Skills, Skill } from '../../appTypes';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import CheckIcon from '@material-ui/icons/Check';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';

import Avatar from '@material-ui/core/Avatar';

import Rating from '@material-ui/lab/Rating';
import { List, ListItem, ListItemText, ListItemIcon, Button } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
            padding: '10px'
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        },
    }),
);

const SkillsPage: React.FC<Skills> = (props): JSX.Element => {
    const classes = useStyles();

    const [list, setList] = useState<Array<Skill>>([]);

    useEffect(() => {
        setList(props.data);
    }, [props.data]);

    return (
        <React.Fragment>
            <Box mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Skills</Typography>
            </Box>
            {list.length > 0 ?
                <Box display="flex" flexWrap="wrap" justifyContent="center" p={1} m={1}>
                    {list.map((skill, index) => (
                        <Box p={1} key={index}>
                            <Card>
                                <CardHeader avatar={<LazyLoad placeholder={<Loader />}><Avatar
                                    variant="rounded"
                                    className={classes.medium}
                                    alt={skill.name}
                                    src={skill.logo} /></LazyLoad>}
                                    title={skill.name} subheader={"known since " + skill.knownSince} />
                                <CardContent>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {skill.yearsOfExperience} year(s) professional work experience
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Rating name="read-only" value={skill.rating} precision={0.5} readOnly />
                                </CardActions>
                            </Card>
                        </Box>
                    ))}
                </Box> :
                <Loader />
            }
            <Box mt={2} p={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Certificates</Typography>
                <List dense={true}>
                    <ListItem>
                        <ListItemIcon><Avatar variant="rounded" className={classes.medium}
                            alt='ROS For Beginners Path'
                            src='images/skills/construct-logo.png' /></ListItemIcon>
                        <ListItemText primary="ROS For Beginners Path"
                            secondary={
                                <React.Fragment>
                                    <Typography component="span" variant="body2" color="textPrimary">
                                        The Construct
                                        </Typography>
                                    <br />Issued Oct 2019 - No Expiration Date<br />
                                    Credential ID RIA3B6359D538C0<br />
                                    <Button href='https://www.robotigniteacademy.com/en/accomplishments/verify/RIA3B6359D538C0/'
                                        endIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        className={classes.buttons}
                                        rel="noopener">See credential</Button>
                                </React.Fragment>
                            } />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><Avatar variant="rounded" className={classes.medium}
                            alt='MCSD: App Builder'
                            src='images/skills/microsoft-logo.png' /></ListItemIcon>
                        <ListItemText primary="MCSD: App Builder"
                            secondary={
                                <React.Fragment>
                                    <Typography component="span" variant="body2" color="textPrimary">
                                        Microsoft
                                    </Typography>
                                    <br />Issued Mar 2018 - No Expiration Date<br />
                                    Credential ID G684-9843<br />
                                    <Button href='https://www.microsoft.com/en-us/learning/mcsd-app-builder-certification.aspx'
                                        endIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        className={classes.buttons}
                                        rel="noopener">See credential</Button>
                                </React.Fragment>
                            } />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><Avatar variant="rounded" className={classes.medium}
                            alt='MCSA: Web Applications'
                            src='images/skills/microsoft-logo.png' /></ListItemIcon>
                        <ListItemText primary="MCSA: Web Applications"
                            secondary={
                                <React.Fragment>
                                    <Typography component="span" variant="body2" color="textPrimary">
                                        Microsoft
                                    </Typography>
                                    <br />Issued Sep 2016 - No Expiration Date<br />
                                    Credential ID F812-0601<br />
                                    <Button href='https://www.microsoft.com/en-us/learning/mcsa-web-applications-certification.aspx'
                                        endIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        className={classes.buttons}
                                        rel="noopener">See credential</Button>
                                </React.Fragment>
                            } />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><Avatar variant="rounded" className={classes.medium}
                            alt='Programming in HTML5 with Javascript and CSS3 Specialist'
                            src='images/skills/microsoft-logo.png' /></ListItemIcon>
                        <ListItemText primary="Programming in HTML5 with Javascript and CSS3 Specialist"
                            secondary={
                                <React.Fragment>
                                    <Typography component="span" variant="body2" color="textPrimary">
                                        Microsoft
                                    </Typography>
                                    <br />Issued May 2014 - No Expiration Date<br />
                                    Credential ID E831-8989<br />
                                    <Button href='https://www.microsoft.com/en-us/learning/exam-70-480.aspx'
                                        endIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        className={classes.buttons}
                                        rel="noopener">See credential</Button>
                                </React.Fragment>
                            } />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><Avatar variant="rounded" className={classes.medium}
                            alt='Microsoft Certified Professional (MCP)'
                            src='images/skills/microsoft-logo.png' /></ListItemIcon>
                        <ListItemText primary="Microsoft Certified Professional (MCP)"
                            secondary={
                                <React.Fragment>
                                    <Typography component="span" variant="body2" color="textPrimary">
                                        Microsoft
                                    </Typography>
                                    <br />Issued Apr 2014 - No Expiration Date<br />
                                    Credential ID E793-5702<br />
                                    <Button href='https://www.microsoft.com/en-us/learning/microsoft-certified-professional.aspx'
                                        endIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        className={classes.buttons}
                                        rel="noopener">See credential</Button>
                                </React.Fragment>
                            } />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><Avatar variant="rounded" className={classes.medium}
                            alt='Microsoft Technology Associate (MTA)'
                            src='images/skills/microsoft-logo.png' /></ListItemIcon>
                        <ListItemText primary="Microsoft Technology Associate (MTA)"
                            secondary={
                                <React.Fragment>
                                    <Typography component="span" variant="body2" color="textPrimary">
                                        Microsoft
                                    </Typography>
                                    <br />Issued Apr 2014 - No Expiration Date<br />
                                    Credential ID E793-5701<br />
                                    <Button href='http://www.microsoft.com/learning/en-us/mta-certification.aspx'
                                        endIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        className={classes.buttons}
                                        rel="noopener">See credential</Button>
                                </React.Fragment>
                            } />
                    </ListItem>
                </List>
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Languages</Typography>
                <List dense={true}>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Turkish" secondary="Native or bilingual proficiency" />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="English" secondary="Professional working proficiency" />
                    </ListItem>
                </List>
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                <p>Beside featured skills at the top, I worked on the following technologies for professional or hobby purposes</p>
                <List dense={true}>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Programming/Scripting Languages" secondary="qbasic, visual basic, fortran, avr assembly, javascript, typescript, html, css" />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Database" secondary="MySQL, MSSQL, Oracle, PostgreSQL, Cassandra, MongoDB" />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Frameworks/Libraries" secondary="Zend, Yii, Spring MVC, ASP.NET MVC5, .net core, bootstrap, jquery, angular, material-ui, tensorflow 2" />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Methodology, Technology and Standarts" secondary="Design Patterns, TDD, DDD" />
                    </ListItem>                    
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Versioning" secondary="SVN, StarTeam, TFS, Git" />
                    </ListItem>  
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Cloud" secondary="Azure, Docker, Kubernetes, SaltStack" />
                    </ListItem>      
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Web Servers" secondary="Apache HTTP, Apache Tomcat, Oracle WebLogic, IIS" />
                    </ListItem>                                                      
                    <ListItem>
                        <ListItemIcon><CheckIcon /></ListItemIcon>
                        <ListItemText primary="Third party applications" secondary="Solidworks, Solidworks Electrical, Proteus, Adobe Fireworks, Adobe Dreamweaver, Adobe InDesign, Adobe Illustrator, Adobe Photoshop" />
                    </ListItem>                    
                </List>
            </Box>
        </React.Fragment>
    );
};

export default SkillsPage;